* {
  font-family: Javan;
}

body {
  background-color: $paper;
  direction: rtl;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}

.App {
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  position: relative;
}

// Screens

@keyframes main-out-left-animation {
  0% {
    right: 0%;
  }

  100% {
    right: 100%;
  }
}

@keyframes main-in-left-animation {
  0% {
    right: 100%;
  }

  100% {
    right: 0%;
  }
}

@keyframes main-in-right-animation {
  0% {
    left: 100%;
  }

  100% {
    left: 0%;
  }
}

@keyframes main-out-right-animation {
  0% {
    left: 0%;
  }

  100% {
    left: 100%;
  }
}

@keyframes main-in-bottom-animation {
  0% {
    bottom: 100%;
  }

  100% {
    bottom: 0%;
  }
}

@keyframes main-out-bottom-animation {
  0% {
    bottom: 0%;
  }

  100% {
    bottom: 100%;
  }
}

@keyframes main-in-top-animation {
  0% {
    top: 100%;
  }

  100% {
    top: 0%;
  }
}

@keyframes main-out-top-animation {
  0% {
    top: 0%;
  }

  100% {
    top: 100%;
  }
}

@keyframes main-out-doo-animation {
  0% {
    transform: rotate(0deg);
    transform-origin: bottom left;
  }

  100% {
    transform: rotate(90deg);
    transform-origin: bottom left;
  }
}

@keyframes main-in-doo-animation {
  0% {
    transform: rotate(90deg);
    transform-origin: bottom left;
  }

  100% {
    transform: rotate(0deg);
    transform-origin: bottom left;
  }
}

@keyframes helper-in-doo-animation {
  0% {
    transform: rotate(-90deg);
    transform-origin: bottom left;
  }

  100% {
    transform: rotate(0deg);
    transform-origin: bottom left;
  }
}

@keyframes helper-out-doo-animation {
  0% {
    transform: rotate(0deg);
    transform-origin: bottom left;
  }

  100% {
    transform: rotate(-90deg);
    transform-origin: bottom left;
  }
}

.main-screen {
  width: 100%;
  height: 100%;
  background-color: $paper;
  position: absolute;
}

.main-out-left {
  right: 100%;
  animation: main-out-left-animation 0.5s ease-in-out;
}

.main-in-right {
  right: 0%;
  animation: main-in-left-animation 0.5s ease-in-out;
}

.main-in-top {
  bottom: 0%;
  animation: main-in-bottom-animation 0.5s ease-in-out;
}

.main-out-top {
  bottom: 100%;
  animation: main-out-top-animation 0.5s ease-in-out;
}

.main-out-doo {
  transform: rotate(90deg);
  transform-origin: bottom left;
  animation: main-out-doo-animation 0.5s ease-in-out;
}

.main-in-doo {
  transform: rotate(0deg);
  transform-origin: bottom left;
  animation: main-in-doo-animation 0.5s ease-in-out;
}

.about-screen {
  width: 100%;
  height: 100%;
  background-color: $text;
  position: absolute;
  left: 100%;
  overflow: auto;
}

.about-screen-in {
  left: 0%;
  animation: main-in-right-animation 0.5s ease-in-out;
}

.about-screen-out {
  left: 100%;
  animation: main-out-right-animation 0.5s ease-in-out;
}

.contact-screen {
  width: 100%;
  height: 100%;
  background-color: $secondary;
  position: absolute;
  bottom: 100%;
}

.contact-screen-in {
  bottom: 0%;
  animation: main-in-top-animation 0.5s ease-in-out;
}

.contact-screen-out {
  bottom: 100%;
  animation: main-out-top-animation 0.5s ease-in-out;
}

.helper-screen {
  // background-color: $secondary_paper;
  width: 100%;
  height: 100%;
  position: absolute;
  // right: 100%;
  transform: rotate(-90deg);
  transform-origin: bottom left;
  overflow: auto;
}

.helper-in-doo {
  transform: rotate(0deg);
  transform-origin: bottom left;
  animation: helper-in-doo-animation 0.5s ease-in-out;
}

.helper-out-doo {
  transform: rotate(-90deg);
  transform-origin: bottom left;
  animation: helper-out-doo-animation 0.5s ease-in-out;
}

//

.main-name-layout {
  //padding: 1em;
  transform: rotate(90deg);
  position: absolute;
  bottom: 13em;
  right: -4.5em;
}

.buttons-col {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 2em;
  top: 1em;
}

.main-projects-layout {
  position: absolute;
  top: 89vh;
  width: 100%;
}

.main-nav-layout {
  transform: rotate(90deg);
  position: absolute;
  top: 5em;
  right: -3em;
}

.name-container {
  font-size: 4em;
  text-align: right;
  display: inline-block;
  overflow: hidden;
}

.name-last-name {
  color: $secondary;
}

.name-first-name {
  color: $primary;
}

.name-main {
  z-index: 1000;
  position: absolute;
  top: 1.4em;
  right: -1.2em;
  min-width: 300px;
}

$columns: 6;

@for $i from 1 through $columns {
  @keyframes name-shadow-animation-#{$i} {
    0% {
      top: 1.4em;
    }

    50% {
      color: $paper;
      //top: 1.6em;
      // top: 2em;
      top: 2.5em;
    }

    100% {
      top: 1.5em; //calc(1.6em + $i * 28em);;
    }
  }
}

.name-shadow {
  color: $secondary_paper;
  display: inline-block;
  position: absolute;
  min-width: 300px;
  right: -1.2em;
  top: 1.4em;

  &-1 {
    top: 1.6em;
    animation: name-shadow-animation-1 1.5s linear 0.5s infinite;
  }

  &-2 {
    top: 1.8em;
    animation: name-shadow-animation-1 1.5s linear 1s infinite;
  }

  &-3 {
    top: 2em;
    animation: name-shadow-animation-1 1.5s linear 1.5s infinite;
  }

  &-4 {
    top: 2.2em;
    animation: name-shadow-animation-1 1.5s linear 2s infinite;
  }

  &-5 {
    top: 2.4em;
    animation: name-shadow-animation-1 1.5s linear 2.5s infinite;
  }

  &-6 {
    top: 2.6em;
    animation: name-shadow-animation-1 1.5s linear 3s infinite;
  }
}

.nav-container {
  color: $text;
  font-size: 2em;
}

.nav-item {
  display: inline-block;
  margin-left: 1em;
  cursor: pointer;
}

.primary-button-container {
  width: 6rem;
  height: 6rem;
  cursor: pointer;
  border-radius: 20em;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.15em;
  margin-top: 0.15em;
  cursor: pointer;
}

.primary-button-container-text {
  border: 2px solid $text;
}

.primary-button-container-secondary {
  border: 2px solid $secondary;
}

.primary-button-container-primary {
  border: 4px solid $secondary_paper;
}

.primary-button {
  width: calc(6rem - 5px);
  height: calc(6rem - 5px);
  border-radius: 20em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.primary-button-text {
  // background-color: $text;
}

.primary-button-secondary {
  background-color: $secondary;
}

.primary-button-primary {
  //  background-color: $primary;
}

.projects-container {
  border-top: 1px solid $text;
 // height: 200vh;
  width: 100%;
  color: $text;
  background-color: $secondary_paper;
}

.projects-title {
  text-align: center;
  font-size: 3em;
}

@keyframes expand-projects-anime {
  0% {
    top: 89vh;
  }

  100% {
    top: 0px;
  }
}

@keyframes shrink-projects-anime {
  0% {
    top: 0px;
  }

  100% {
    top: 89vh;
  }
}

.expand-button {
  border: 2px solid $text;
  background-color: $secondary_paper;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  position: absolute;
  top: -30px;
  left: 30px;
  cursor: pointer;
}

@keyframes expand-to-shrink-button {
  0% {
    top: -30px;
    transform: rotate(0deg);
  }

  50% {
    top: 1em;
    transform: rotate(90deg);
  }

  100% {
    top: 2em;
    transform: rotate(180deg);
  }
}

@keyframes shrink-to-expand-button {
  0% {
    top: 2em;
    transform: rotate(180deg);
  }

  50% {
    top: 1em;
    transform: rotate(90deg);
  }

  100% {
    top: -30px;
    transform: rotate(0deg);
  }
}

.expand-to-shrink {
  top: 2em;
  transform: rotate(180deg);
  animation: expand-to-shrink-button 200ms linear;
}

.shrink-to-expand {
  top: -30px;
  transform: rotate(0deg);
  animation: shrink-to-expand-button 200ms linear;
}

.expand-projects {
  top: 0px;
  animation: expand-projects-anime 1000ms ease-in;
}

.shrink-projects {
  top: 89vh;
  animation: shrink-projects-anime 1000ms ease-in;

}

// About Screen

.about-pic-avatar-container {
  width: 20em;
  height: 20em;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10em;
  position: relative;
  right: -4em;
  top: 0.5em;
  display: inline-block;
  z-index: 50;
}

.about-pic-avatar-overlay {
  width: 20em;
  height: 20em;
  background-color: rgba(0, 18, 51, 0.4);
  position: absolute;
  border-radius: 10em;
  z-index: 100;
}

.about-pic-avatar {
  width: 20em;
  height: 20em;
}

.about-decorative {
  height: 100%;
  width: 2em;
  background-color: $primary;
  position: absolute;
  top: 0px;
  right: 1em;
}

.about-vertical-decorative {
  height: 5em;
  width: 100vw;
  background-color: $secondary_paper;
  position: absolute;
  top: 4em;
  right: 0em;
}

.about-description {
  position: absolute;
  color: $paper;
  font-size: 1.5em;
  max-width: 400px;
  right: 3em;
  left: 1em;
}

@keyframes back-arrow {
  0% {
    transform: rotate(-70deg);
  }

  50% {
    transform: rotate(-110deg);
  }

  100% {
    transform: rotate(-70deg);
  }
}

.about-back-button {
  border: 2px solid $secondary;
  background-color: $secondary_paper;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  position: absolute;
  top: 3.2em;
  left: 2em;
  transform: rotate(-90deg);
  animation: back-arrow 0.3s ease-in-out infinite;
  cursor: pointer;
}

// Contact Screen

