  border-radius: 100px;
  position: absolute;
  top: 3.2em;
  left: 2em;
  transform: rotate(-90deg);
  animation: back-arrow 0.3s ease-in-out infinite;
  cursor: pointer;
}

.contact-container {
  padding: 2em;
}

.my-email {
  font-size: 1.6em;
  margin-top: 4em;
}

.my-email > a {
  color: $primary !important;
  text-decoration: none;
}

.contact-para {
  color: $primary;
  font-size: 1.5em;
  max-width: 400px;
}

.question {
  max-width: 400px;
}

.helper-back-button {
  border: 2px solid $secondary;
  background-color: $paper;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  position: fixed;
  top: 3.2em;
  left: 2em;
  transform: rotate(-90deg);
  animation: back-arrow 0.3s ease-in-out infinite;
  cursor: pointer;
}

